// interface do video html elementos
// o protocolo que seriam os métodos de play, stop e eventos
// e a classe que lidará com isso

interface VideoPlayerElements {
  videoPlayer: HTMLVideoElement;
  playButton: HTMLButtonElement;
  stopButton: HTMLButtonElement;
}

interface VideoPlayerProtocol {
  playVideo(): void;
  stopVideo(): void;
  startEvents(): void;
}

export class VideoPlayer implements VideoPlayerProtocol {
  private videoPlayer: HTMLVideoElement;
  private playButton: HTMLButtonElement;
  private stopButton: HTMLButtonElement;

  constructor(videoPlayerElements: VideoPlayerElements) {
    this.videoPlayer = videoPlayerElements.videoPlayer;
    this.playButton = videoPlayerElements.playButton;
    this.stopButton = videoPlayerElements.stopButton;
  }

  playVideo(): void {
    console.log('play');
    if (this.videoPlayer.paused) {
      this.playButton.textContent = 'Pause';
      this.videoPlayer.play();
      return;
    }
    this.playButton.textContent = 'Play';
    this.videoPlayer.pause();
  }

  stopVideo(): void {
    if (!this.videoPlayer.paused) this.videoPlayer.pause();
    this.videoPlayer.currentTime = 0;
  }

  startEvents(): void {
    this.playButton.addEventListener('click', () => {
      this.playVideo();
    });
    this.stopButton.addEventListener('click', () => {
      this.stopVideo();
    });
  }
}

const videoPlayer = document.querySelector('.video') as HTMLVideoElement;
const playButton = document.querySelector('.play') as HTMLButtonElement;
const stopButton = document.querySelector('.stop') as HTMLButtonElement;

const player = new VideoPlayer({ videoPlayer, playButton, stopButton });
player.startEvents();
